h1 {
  color: tomato;
}

.borderedBox {
  margin-top: 50px;
  width: 100%;
  height: 80vh;
  border: 5px solid black;
}

.list {
  height: 20vh;
}

.singleBox, .videoBox {
  height: 65vh;
}

.singleBox {
  margin: 50px auto;
  width: 400px;
  border: 5px solid black;
}

.drawingBox {
  height: 65vh;
  margin: 50px auto;
  width: 70%;
  border: 5px solid black;
}

.startCampaign {
  width: 30%;
  margin-left: 35%;
  margin-right: 35%;
  height: 10%;
  margin-top: 32vh;
}

.saveButton {
  width: 40%;
  margin-left: 30%;
  margin-right: 30%;
  margin-top: 1vh;
  border-radius: 10px;
  height: 6vh;
}


.saveOrder {
  width: 20%;
  margin-left: 40%;
  margin-right: 40%;
  margin-top: -4vh;
  border-radius: 10px;
  height: 6vh;
}

.videoBox {
  margin: 50px auto;
  width: 600px;
}

.centerVideosHeader {
  margin-top: 10px;
  text-align: center;
}

.line {
  width: 100%;
  height: 5px;
  background-color: black;
}

.listItem {
  font-size: 40px;
}

.bottomRight, .bottomLeft {
  position: relative;
}


.bottomLeft {
  top: -10.5vh;
}

.bottomRight {
  top: -5vh;
  left: 90%;
}

.arrow {
  border: solid black;
  border-width: 0 5px 5px 0;
  height: 40px;
  width: 40px;
}

.right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.left {
  margin-left: 20px;
  margin-top: 50px;
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
}

.arrowHolder{
  margin-top: 200px;
}

.introTitle {
  margin-top: 5vh;
  font-size: 2vw;
  text-align: center;
  width: 100%;
}

.introSubtitle {
  margin-top: 5vh;
  font-size: 1.7vw;
  text-align: center;
  width: 100%;
}

.centreForm {
  width: 70%;
  margin-left: 15%;
}

.curatorBox, .clientBox {
  border: 2px solid black;
  height: 10vh;
  width: 20%;
  margin-top: 12vh;
  margin-left: 10%;
}

.clientBox {
  margin-left: 40%;
}

.imageUpload {
  font-size: 0.7em;
  margin: 0 auto;
  text-align: center;
}

.sendButton {
  margin-top: 10vh;
  width: 300%;
}

.textSendButton {
  margin-top: 2vh;
  width: 300%;
}

.butMarginTop {
  margin-top: 5vh;
}
.saveImageButton{
  margin-top: 5vh;
}